<template>
  <van-row justify="center">
    <van-col span="12">
      <div class="auth-provider">
         <h1>Select provider</h1>
         <van-button @click="gAuth" color="#7232dd" >Login with Google</van-button>
      </div>
    </van-col>
  </van-row>
</template>

<script>
import { Button } from 'vant'
import firebase from '@/init-firebase.js'

export default {
  name: 'Login',
  data () { return {}; },
  components: {
    'van-button': Button
  },
  methods: {
   gAuth() {
     const self = this;
     const provider = new firebase.auth.GoogleAuthProvider();
     firebase.app.auth().signInWithPopup(provider)
       .then(function(result) {
         // This gives you a Google Access Token. You can use it to access the Google API.
         //var token = result.credential.accessToken;
         // The signed-in user info.
         var user = result.user;
         console.log('success ', result.user, result)
         self.$store.commit('updateUser', result.user)
//         self.$store.dispatch('getPersistedState')
         self.$router.push({ name: 'Home' });
       }).catch(function(error) {
         // Handle Errors here.
         var errorCode = error.code;
         var errorMessage = error.message;
         // The email of the user's account used.
         var email = error.email;
         // The firebase.auth.AuthCredential type that was used.
         var credential = error.credential;
         console.log('error ', error)
         self.$router.push({name: 'Home'})
       });
   }
  }
}
</script>

